body {
  background-color: #fff;
  color: #333;
  font-family: Arial, Verdana, sans-serif;
  font-size: 2em;
  text-align: left;
}

button {
  font-size: 2em;
  margin-bottom: 0.1em;
}
#result {
  color: #000;
  font-size: 3em;
  font-weight: bold;
  text-align: center;
  width: 400px;
  margin-left: auto;
  margin-right: auto;
}

#App {
  clear: both;
  font-size: 3em;
  width: 400px;
  margin-left: auto;
  margin-right: auto;
}

input[type=text] {
  border: 1px solid #ccc;
  color: #ccc;
  font-size: 3em;
    margin-bottom: 0.5em;
}

#min {
  width: 300px;
}

#max {
  width: 300px;
}

#count {
  width: 300px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

